
<template>
  <!--
  element-ui
  props
  slot
  ,plain
  v-on@DOM
  -->
  <button class="ct-button" v-bind:class="[`ct-button--${type}`,`ct-button--${size}`,
  {'is-plain':plain},
  {'is-round':round},
  {'is-circle':circle},
  {'is-disabled':disabled},
  {'is-loading':loading}]"
  @click="handleClick"
          :disabled="disabled || loading"
          :loading="loading"
  >
    <i class="ct-icon-loading" v-if="loading"></i>
    <i  v-if="icon&&!loading" :class="`iconfont icon-${icon} `"></i>
    <!--span-->
    <span v-if="$slots.default"><slot></slot></span>
  </button>
</template>

<script>
export default {
  name: "CtButton",
  //provideinject
  inject:{
    ctForm:{
      default:'',
    },
    ctFormItem:{
      default:'',
    }
  },
  //propsstringtype
  props:{
    size:{
      type:String,
      default:'14px',
    },
    type:{
      type:String,
      //default
      default:'default',
    },
    plain:{
      type:Boolean,
      default:false
    },
    round:{
      type:Boolean,
      default:false,
    },
    circle:{
      type:Boolean,
      default:false,
    },
    loading:{
      type:Boolean,
      default:false,
    },
    disabled:{
      type:Boolean,
      default:false,
    },
    icon:{
      type:String,
      default:"",
    },
    autofocus:{
      type:Boolean,
      default:false,
    }
  },
  created() {
    console.log(this.type);
  },
  computed:{
    _ctFormItemSize(){
      return (this.ctFormItem||{}).ctFornItemSize;
    },
    buttonSize() {
      return this.size || this._ctFormItemSize || (this.$ELEMENT || {}).size;
    },
    buttonDisabled() {
      return this.disabled || (this.ctForm || {}).disabled;
    }
  },
  methods: {
    handleClick(evt) {
      this.$emit('click', evt);
    }
  }
}
</script>

<style lang="less">
  @import "./UI/theme/button.less";
</style>