import Vue from "vue";

const NoticeConstructor = Vue.extend(require("./main.vue").default); // VueVue.extend,vue-loader.default

let nId = 1;

const Message = (options) => {
  let id = "notice-" + nId++;
  options = options || {};
  if (typeof options === "string") {
    //
    options = {
      message: options, //messagemessage.vuedatamessage
    };
  }

  const NoticeInstance = new NoticeConstructor({
    // contentNotice
    data: options, //message.vuedata
  });
  //console.log(NoticeInstance);
  NoticeInstance.id = id;
  NoticeInstance.vm = NoticeInstance.$mount(); // domVue
  NoticeInstance.vm.visible = true; //message.vuevisible,message
  //console.log(NoticeInstance.vm);
  NoticeInstance.dom = NoticeInstance.vm.$el; //dom
  document.body.appendChild(NoticeInstance.dom); // dombody
  if (NoticeInstance.dom.length > 0) {
    NoticeInstance.dom.style.zIndex = nId + 1001; // Noticez-index
  }
  return NoticeInstance.vm;
};

["success", "warning", "info", "error"].forEach((type) => {
  //element-uiMessage
  Message[type] = (options) => {
    if (typeof options === "string") {
      options = {
        message: options,
      };
    }
    options.type = type;
    return Message(options);
  };
});

export default Message;
