import Vue from "vue";
import App from "./App.vue";

//
import fanui from "../UI";
//
import router from "./router";
//
import "./assets/iconfont/font.css";
//
import "./assets/basecss/base.css";
import "./assets/basecss/global.css";

//
import vuescroll from "vuescroll";
import "vuescroll/dist/vuescroll.css";

// highlight
import Highlight from "./plugins/highlight"; // highlight.js
Vue.use(Highlight);


Vue.use(fanui);
Vue.use(vuescroll);

Vue.config.productionTip = false;


Vue.directive('animate', {
  //  DOM 
  inserted: function (el, binding) {
    // 
    binding.addClass = () => {
      const { top } = el.getBoundingClientRect()
      const h = document.documentElement.clientHeight || document.body.clientHeight
      if (top < h) {
        if(el.className.indexOf(binding.value) == -1 ){
          // ()
          el.className = binding.value+' '+el.className
        }
        if (binding.addClass) {
          window.removeEventListener('scroll', binding.addClass)
        }

      }
    }
    window.addEventListener('scroll', binding.addClass)
    binding.addClass()
  },

  unbind: function (el, binding) {
    if (binding.addClass) {
      window.removeEventListener('scroll', binding.addClass)
    }
  }

});

new Vue({
    render: (h) => h(App),
    router,
}).$mount("#app");